body {
  --primary-color: #000;
  --active-color: #00ACDC;
  --screen-l: 1024px;

  margin: 0;
}

.roboto-500 {
  font-family: Roboto, sans-serif;
  font-variation-settings: "wdth" 100;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 50px;
}

.menu {
  display: flex;
  flex-direction: row;
  gap: 0 20px;
  justify-content: space-between;

  height: 100%;
  margin: 0;
}

.menu-item {
  margin-left: 20px;
  position: relative;
  display: flex;
  align-items: center;
  list-style: none;
}

.menu-item:first-child {
  margin-left: 0px;
}

.menu-item a {
  font-size: 12px;
  line-height: 100%;
  color: var(--primary-color);
  text-decoration: none;
  text-transform: uppercase;
}

.menu-item a:hover {
    color: var(--active-color);
}

.menu-item:has(a.is-active)::after {
  content: "";

  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;

  display: block;

  width: 100%;
  height: 4px;
  border-radius: 8px;

  background-color: var(--active-color);
}

.menu-item a.is-active {
  color: var(--active-color);
}

.logo {
  width: 40px;
  height: 40px;
  margin: 10px 0;
}

