body {
  --primary-color: #000;
  --active-color: #00acdc;
  --screen-l: 1024px;
  margin: 0;
}

.roboto-500 {
  font-variation-settings: "wdth" 100;
  font-optical-sizing: auto;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.header {
  flex-direction: row;
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.menu {
  flex-direction: row;
  justify-content: space-between;
  gap: 0 20px;
  height: 100%;
  margin: 0;
  display: flex;
}

.menu-item {
  align-items: center;
  margin-left: 20px;
  list-style: none;
  display: flex;
  position: relative;
}

.menu-item:first-child {
  margin-left: 0;
}

.menu-item a {
  color: var(--primary-color);
  text-transform: uppercase;
  font-size: 12px;
  line-height: 100%;
  text-decoration: none;
}

.menu-item a:hover {
  color: var(--active-color);
}

.menu-item:has(a.is-active):after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.menu-item a.is-active {
  color: var(--active-color);
}

.logo {
  width: 40px;
  height: 40px;
  margin: 10px 0;
}
